// gantt.vue
import gantt from './gantt.vue'

// 
const myPlugin = {
  // install
  // Vue
  // eslint-disable-next-line
    install (Vue, options) {
    // vue'gantt'keyboard
    Vue.component('v-gantt-chart', gantt)
  }
}
// 
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(myPlugin)
}

// main.jsVue.use()
export default myPlugin
