let dynamicRender = {
  props: {
    scrollTop: {
      type: Number,
      required: true
    },
    heightOfRenderAera: {
      type: Number,
      required: true
    },
    cellHeight: {
      type: Number,
      required: true
    },
    datas: {
      type: Array,
      required: true
    }
  },

  data() {
    return {
      //
      oldTopIndex: 0,
      //,2
      preload: 1, //  0 ,
      startRenderNum: 0,
      endRenderNum: 0
    };
  },

  computed: {
    blockHeight() {
      let {
        datas,
        cellHeight
      } = this;
      return datas.length * cellHeight;
    },
    //index
    currentTopIndex() {
      return Math.ceil(this.scrollTop / this.cellHeight);
    },
    showDatas() {
      const {
        startRenderNum,
        endRenderNum,
        datas
      } = this
      return datas.slice(startRenderNum, endRenderNum);
    },
    /**
     * 
     */
    topSpace() {
      let {
        oldTopIndex,
        cellHeight,
        preload
      } = this;
      let start =
        oldTopIndex - preload > 0 ? oldTopIndex - preload : 0;
      return start * cellHeight;
    }
  },

  watch: {
    currentTopIndex(val) {
      let {
        oldTopIndex,
        preload
      } = this;
      if (preload === 0) {
        this.sliceData();
        return
      }
      if (oldTopIndex === val) {
        return
      }
      // spliceData 
      let errorValue = 1 // 
      if (val < oldTopIndex - (preload - errorValue) || val >
        oldTopIndex + (preload - errorValue)) {
        this.oldTopIndex = val;
        this.sliceData();
      }
    },
    datas() {
      this.sliceData()
    },
    heightOfRenderAera() {
      this.sliceData()
    },
    cellHeight() {
      this.sliceData()
    }
  },

  created() {
    this.sliceData();
  },

  methods: {
    /**
     * dom
     */
    sliceData() {
      let {
        heightOfRenderAera,
        currentTopIndex,
        cellHeight,
        preload
      } = this;
      //
      if (heightOfRenderAera === 0 || cellHeight === 0) {
        this.endRenderNum = 0
        this.startRenderNum = 0
        return
      }

      //  0 
      if (preload === 0) {
        this.endRenderNum = Infinity
        this.startRenderNum = -Infinity
        return
      }

      this.endRenderNum = currentTopIndex + Math.ceil(heightOfRenderAera /
          cellHeight) +
        preload;
      this.startRenderNum = currentTopIndex - preload > 0 ? currentTopIndex -
        preload : 0;
    }
  }
};

export default dynamicRender;